namespace $ { export class $mol_form extends $mol_view {

	/// submit_blocked false
	submit_blocked() {
		return false
	}

	/// form_fields /
	form_fields() {
		return [] as any[]
	}

	/// barFields $mol_view sub <= form_fields
	@ $mol_mem()
	barFields() {
		return new $mol_view().setup( obj => { 
			obj.sub = () => this.form_fields()
		} )
	}

	/// buttons /
	buttons() {
		return [] as any[]
	}

	/// barButtons $mol_row sub <= buttons
	@ $mol_mem()
	barButtons() {
		return new $mol_row().setup( obj => { 
			obj.sub = () => this.buttons()
		} )
	}

	/// sub / 
	/// 	<= barFields 
	/// 	<= barButtons
	sub() {
		return [].concat( this.barFields() , this.barButtons() )
	}

} }

