namespace $ { export class $mol_form_field extends $mol_view {

	/// name \
	name() {
		return ""
	}

	/// namer $mol_view sub / <= name
	@ $mol_mem()
	namer() {
		return new $mol_view().setup( obj => { 
			obj.sub = () => [].concat( this.name() )
		} )
	}

	/// errors /
	errors() {
		return [] as any[]
	}

	/// bider $mol_view sub <= errors
	@ $mol_mem()
	bider() {
		return new $mol_view().setup( obj => { 
			obj.sub = () => this.errors()
		} )
	}

	/// label $mol_view sub / 
	/// 	<= namer 
	/// 	<= bider
	@ $mol_mem()
	label() {
		return new $mol_view().setup( obj => { 
			obj.sub = () => [].concat( this.namer() , this.bider() )
		} )
	}

	/// control null
	control() {
		return <any> null
	}

	/// sub / 
	/// 	<= label 
	/// 	<= control
	sub() {
		return [].concat( this.label() , this.control() )
	}

} }

